import { Component, OnInit } from '@angular/core';
import { MenuController } from '@ionic/angular';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateConfigService } from '../../services/translate-config.service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';
import { MenuOptions } from 'src/app/app.component';

@Component({
  selector: 'app-dashboard-before-login',
  templateUrl: './dashboard-before-login.page.html',
  styleUrls: ['./dashboard-before-login.page.scss'],
})
export class DashboardBeforeLoginPage  {
  // public folder!: string;
  selectedLanguage: string;

  constructor(
    private activatedRoute: ActivatedRoute,
    private translateConfigService: TranslateConfigService,
    public menuControl: MenuController,
    private router: Router,
    public config: ConfigProvider,
    public utils: UtilService
  ) {
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();

    this.translateConfigService.setLanguage('en');
  }


  ionViewWillEnter() {
   

    // this.utils.component.emit(MenuOptions.MY_HOME_PAGE);
  }
  ionViewDidEnter() {
    // this.menuControl.swipeGesture(false);
    // this.menuControl.enable(true, 'first');
    // this.menuControl.enable(false);
    this.config.currentPage.pageName = "DashboardBeforeLoginPage";
  }


  contactUs() {
    this.router.navigate(['/contact-us'], { replaceUrl: true })
  }
  aboutUs() {
    // this.menuControl.open();
    this.router.navigate(['/about-us'], { replaceUrl: true });
  }
  unitPrice() {
    this.router.navigate(['/unit-price'], { replaceUrl: true });
    

  }
  showOpenPdf() {
    var UserGuideLink = "https://www.sunlifemalaysia.com/getmedia/939a7f39-52fd-441a-9b85-64c810208b48/SunAccess-UserGuide.aspx"
    this.utils.openLink(UserGuideLink);
  }
  register() {
    this.router.navigate(['/sign-up']);
  }
  /**
   * This method is used to navigate to Login Page
   */
  login = function () {
    if (this.config.passCodeInfo?.hasPasscodeAuth) {
      let showFingerData = { showFingerPrint: 'true' };
      this.router.navigate(['/login-first'], { state: { showFingerData: showFingerData } });
    } else {
      this.router.navigate(["login-first"]);

    }
  }
}
